import {Route} from "@slyte/router";
import { MangerComp } from "/components/javascript/manger-comp";

class Manager extends  Route {
    beforeFetch(){
		var role = this.$app.$ServiceProvider.getRole();
		  if(role != "Manager"){
				this.$app.$ServiceProvider.goTo(role);
			}
	}
    render(){
        return {
            outlet : "#outlet",
            component : MangerComp
        }
    }
    static actions(){
        return {

        }
    }
}

export {Manager};

